<template>
  <div>
    <ul class="newList">
      <li class="newItem"
        v-for='(item,index) in pageLists' 
        :key='index'>
        <p class="title">{{item.title}}</p>
        <p class="content">{{item.content}}</p>
        <p class="time">{{item.date}}</p>
      </li>
    </ul>  
  </div>
</template>

<script>
  import store from '@/store';
  export default {
    name: 'list',
    store,
    computed: {
      pageLists () {
        return store.state.lists
      }
    }
      
  }
</script>

<style lang="scss">
  .newList{
    padding: 10px;
  }
  .newItem{
    padding: 5px;
    margin-bottom: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 3px rgba(0,0,0,.3);
    .title{
      padding-bottom: 4px;
      border-bottom: 1px solid #ccc;
    }
    .content{
      font-size:14px;
    }
    .time{
      font-size:12px;
    }
  }
</style>